import React, { useState, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'

import { change_city } from '../../redux/action' //redux
import { useSelector, useDispatch } from 'react-redux' //redux

import { List } from 'antd-mobile' //antd-mobile
import PublicHeader from '../../components/PubHeader' //
import './style.less'

import { getAllCities } from '../../api'

export default function City() {
  const navigate = useNavigate()
  const dispatch = useDispatch()

  const [cityArr] = useState(['', '', '', '', ''])
  const [allCities, setAllCities] = useState([])

  const city = useSelector(state => state.city.cityName)

  //
  useEffect(() => {
    //
    getAllCities().then(res => {
      console.log(res)
      setAllCities(res.data)
    }).catch(err=>{
      console.log(err)
    })
  }, [])

  //
  function changeCity(cityName) {
    dispatch(change_city(cityName))
    navigate('/')
  }

  return (
    <>
      <PublicHeader />

      {/*  */}
      <div className='current-city'>
        <h2>{city}</h2>
      </div>

      {/*  */}
      <div className="city-list-container">
        <h3></h3>
        <ul className="clear-fix">
          {cityArr.map((city, index) => {
            return (
              <li key={index} onClick={() => changeCity(city)}>
                <span>{city}</span>
              </li>
            )
          })}
        </ul>
      </div>

      {/*  */}
      <List header=''>
        {
          allCities.map((cityobj,index) => {
            return (
              <List.Item key={index} onClick={()=> changeCity(cityobj)}>{cityobj}</List.Item>
            )
          })
        }


      </List>
    </>

  )
}
